<template>
<div>
    <h2></h2>
    <el-form label-width="120px" @submit.native.prevent="create">
        <el-form-item label="">
            <el-select v-model="model.categories" placeholder="">
                <el-option v-for="item in parents" :key="item._id" :label="item.name" :value="item._id">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="">
            <el-input v-model="model.title"></el-input>
        </el-form-item>
        <el-form-item label="">
            <VueEditor useCustomImageHandler @image-added="handleImageAdded" v-model="model.body"></VueEditor>
        </el-form-item>

        <el-form-item>
            <el-button type="primary" native-type="submit"></el-button>
        </el-form-item>
    </el-form>
</div>
</template>

<style>

</style>

<script>
import {
    VueEditor
} from 'vue2-editor';
export default {
    data() {
        return {
            model: {
               
            },
            parents: []
        }
    },
    components: {
        VueEditor,
    },
    created() {
        this.getParents();
    },
    methods: {
        async create() {
            console.log("", this.model)
            const result = await this.$http.post('restful/articles/', this.model)
            console.log(result);
            this.$message({
                message: '',
                type: 'success'
            });
            this.$router.push('/article/list')
        },

        // 
        async getParents() {
            const result = await this.$http.get('restful/categories/')
            console.log(result);
            this.parents = result.data
        },
        // 
        async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
            const formData = new FormData();
            formData.append('file', file);
            const result = await this.$http.post('/upload', formData);

            const url = result.data.url;
            // 
            Editor.insertEmbed(cursorLocation, 'image', url);
            resetUploader()

        }
    },
};
</script>
