<template>
<div class="articleList">
    <h2></h2>
    <el-table :data="model">
        <el-table-column prop="_id" label="id" width="240">
        </el-table-column>
        
        <el-table-column prop="title" label="" width="220">
        </el-table-column>
        

        <el-table-column label="" width="260">
            <template slot-scope="scope">
                <el-button @click="del(scope.row)" type="danger" size="small"></el-button>
                <el-button type="primary" @click="$router.push(`/article/edit/${scope.row._id}`)" size="small"></el-button>
            </template>
        </el-table-column>

    </el-table>
</div>
</template>

<script>
export default {
    components: {},
    props: {},
    data() {
        return {
            model: []
        }
    },

    methods: {
        // 
        async fetch() {
            const res = await this.$http.get('restful/articles');
            this.model = res.data
        },

        async del(item) {

            this.$confirm(`"${item.title}", ?`, '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {

                let res = await this.$http.delete(`restful/articles/${item._id}`)
                console.log(res)
                this.$message({
                    message: '',
                    type: 'success'
                })
                this.fetch()

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });

        }
    },
    created() {
        this.fetch()
    },
    mounted() {}
}
</script>

