<template>
  <div class="categoryList">
    <h2></h2>
    <el-table :data="model.result">
      <el-table-column prop="_id" label="id" width="240"> </el-table-column>
      <el-table-column prop="name" label="" width="220">
      </el-table-column>
      <el-table-column prop="title" label="" width="220"> </el-table-column>
      <el-table-column prop="avatar" label="" width="220">
        <template slot-scope="scope">
          <img :src="scope.row.avatar" style="height:3em" alt="" />
        </template>
      </el-table-column>

      <el-table-column label="" width="260">
        <template slot-scope="scope">
          <el-button @click="del(scope.row)" type="danger" size="small"
            ></el-button
          >
          <el-button
            type="primary"
            @click="$router.push(`/heroes/edit/${scope.row._id}`)"
            size="small"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-row style="margin-top:2rem">
      <el-col :offset="6" :span="16">
        <el-pagination
        @current-change="changePage"
        @prev-click ="prevClick"
         background layout="prev, pager, next" :total="model.count">
        </el-pagination>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      model: {},
      currentPage:1
    };
  },

  methods: {
    // 
    async fetch() {
      const res = await this.$http.get("restful/heroes",{
        params:{
          page:this.currentPage,
          limit:10
        }
      });
      this.model = res.data;
    },

    // 
    changePage(page){
    
      this.currentPage = page;
      this.fetch()
    },
    // 
    prevClick(e){
      console.log(e);
      console.log("")
    },

    async del(item) {
      this.$confirm(`"${item.name}", ?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          let res = await this.$http.delete(`restful/heroes/${item._id}`);
          console.log(res);
          this.$message({
            message: "",
            type: "success",
          });
          this.fetch();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
  created() {
    this.fetch();
  },
  mounted() {},
};
</script>

<style lang="scss" scoped></style>
