<template>
<div>
    <h2></h2>
    <el-form label-width="120px" @submit.native.prevent="save">
        
        <el-form-item label="">
            <el-input v-model="model.username"></el-input>
        </el-form-item>
          <el-form-item label="">
            <el-input v-model="model.password"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" native-type="submit"></el-button>
        </el-form-item>
    </el-form>
</div>
</template>

<style>

</style>

<script>
export default {
    data() {
        return {
            model: {
                username: '',
            },
        }
    },
    props:{
        id:String
    },
    methods: {
        async save() {
            
            const result = await this.$http.put(`restful/adminUsers/${this.id}`, this.model)
            console.log(result);
            this.$message({
                message: '',
                type: 'success'
            });
            this.$router.push('/admin_users/list')
        },

        async getDetailModel(){
            const result = await this.$http.get(`restful/adminUsers/detail/${this.id}`)
            console.log(result);
            
            this.model=result.data
        },
         
    },
    created () {
        this.getDetailModel()
       
    },
};
</script>
